.button_black {
  color: #000;
  border: 1px solid #000;
  background-color: rgba(0, 0, 0, .1);
  display: inline-block;
  cursor: pointer;
  transition: .2s linear;
}

.button_black:hover {
  box-shadow: 0px 1px 5px rgba(0, 0, 0, .4);
  background-color: rgba(0, 0, 0, .1);
}

.button_black:focus {
  outline: none;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, .4);
  background-color: rgba(0, 0, 0, 0.1);
}

.button_black:active {
  box-shadow: 0px 0px 3px rgba(0, 0, 0, .4);
  background-color: rgba(0, 0, 0, .1);
}